.chat {
    /* flex: 0.70; */
    /* background-color: black; */
    display: flex;
    flex-direction: column;
}

.chat__header {
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat__headerInfo > h3 {
    margin-bottom: 3px;
    font-weight: 500;
}

.chat__headerInfo  > p {
    color: gray;
}

.chat__headerRight {
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat__body {
    background-image: url("https://i.pinimg.com/originals/ab/ab/60/abab60f06ab52fa7846593e6ae0c9a0b.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    height: 66vh;
    overflow: auto;
}

.chat__message {
    position: relative;
    font-size: 16px;
    padding: 10px;
    background-color: white;
    border-radius: 10px;
    width: fit-content;
    margin-bottom: 20px;
}

.chat__name {
    position: absolute;
    top: -15px;
    font-weight: 800;
    /* min-width: -200px; */
    font-size: xx-small;
}

.chat__timestamp {
    margin-left: 10px;
    font-size: xx-small;
}

.chat__reciever {
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    height: 62px;
    border-top: 1px solid lightgray;
}

.chat__footer > form {
    flex: 1;
    display: flex;
}

.chat__footer > form > input {
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
}

.chat__footer > .MuiSvgIcon-root {
    padding: 10px;
    color: gray;
}

.chat__footer > form > button {
    /* display: none; */
}

@media screen and (min-width: 960px) {
    .padding-custom {
        padding: 0 !important;
    }
}